.about-container {
    height: 100vh;
    /* overflow: hidden; */
    position: relative;
}

.about-content{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 600px;
    width: 1000px;
    background-color: black;
    display:flex;
}

.about-left{
    position: relative;
    overflow: hidden;
    background-color: white;
    width: 48%;
    
}

.about-right {
    background-image: radial-gradient(#c75346, #9f5148);
    width: 60%;
    border-left: 10px solid white;
}




.about-title{
    margin: 0 auto 50px;
    text-align: center;
    font-size: 2rem;
    padding: 20px 40px;
}

.code-container {
    height: 600px;
    background-color: rgb(63, 63, 63);
    overflow: auto;
    box-shadow: inset 0 0 20px black;
    color: white;
    padding: 30px;
    font-size: 1rem;
    font-family: monospace;
}


.code-container a {
    all: unset;
    text-decoration: underline;
    cursor: pointer;
}

.cB {color: #3487b9}
.cG {color: #00ccaf}
.cGd {color: #ffd500}
.cBe{color:#d9d9a2}
.cP{color:#e968db}
.cLB{color:#88deff}
.cO{color:#d98e73}
.cM{color:#afcfa4}
.cLink{text-decoration: underline}

