article {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: all 0.5s ease;
  }
  .activeSlide {
    opacity: 1;
    transform: translateX(0);
  }
  .lastSlide {
    opacity: 0;
    transform: translateX(-100%);
  }
  .nextSlide {
    transform: translateX(100%);
    opacity: 0;
  }

  .carousel-container {
    height: 100%;
  }

  .carousel {
    width: 600px;
    display: flex;
    height: 100%;
    transition: all 0.3s linear;
  }

  .carousel article {
    transition: all 0.5s ease;
  }

  .carousel img {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    object-fit: cover;
    aspect-ratio: 4/5;
  }

  .carousel button {
    position: absolute;
    transform: translateY(-50%);
    z-index: 2;
  }

 .prev {
    top: 50%;
    left: 0;
  }

  .next {
    top: 50%;
    right: 0;
  }

